@import "./variables.scss";

.flex {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 16px;
}

#content {
    display: flex;
    justify-content: center;
}

.center {
    max-width: 1408px;
}

#intro {
    height: 240px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 84px;

    h1{
        margin-bottom: 24px;
    }

    a {
        color: $primary-color;
    }
}

#articles {
    padding: 0 84px;
}

.article-card {
    display: flex;
    justify-content: space-between;
    padding: 24px 0;
}

.article-img-wrap {
    height: 300px;
    aspect-ratio: 3 / 2; 
    background-size: cover;
    background-position: center;
    border-radius: 16px;
}

#deltaimg {
    background-image: url('./assets/cover-delta.png');
}

#dittoimg {
    background-image: url('./assets/cover-ditto.png');
}

#breastimg {
    background-image: url('./assets/cover-breasts.png');
}

.article-text {
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 48px;
    h2 {
        margin-bottom: 12px;
        line-height: 1.4;
        text-transform: capitalize;
    }
}

.article-tag {
    color: #45a8af;
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.article-intro {
    color: #9e9e9e;
    line-height: 1.5;
    max-height: 70px;
    overflow: hidden;
    margin-bottom: 24px;
}